import React, { Component } from 'react'
import App from './App'

export default class CPU extends Component {
    state = { 
        messages: "none",
        theme: {
            header: "white",
            footer: "white",
            message: "white",
            chatBox: "white",
            textStyleEditor: "white",
            color: "black"
        }
    }
    // darkTheme = setState({
    //     theme: {
    //         header: "black",
    //         footer: "black",
    //         message: "black",
    //         chatBox: "black",
    //         textStyleEditor: "black",
    //         color: "white"
    //     }
    // })
    render() {
        return (
            <App message={this.state.messages} theme={this.state.theme}/>
        )
    }
}
