import React from 'react';
import {Button} from '@material-ui/core';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

function Login() {
    return (
        <Container>
            <div>
            <h1>The Login feature is under construction</h1>
            <h2>Join Anonymous Chat Room</h2>
            <Button variant="contained" color="primary">
                <Link to="room">Here</Link>

            </Button>

            </div>
        </Container>
    )
}

export default Login
const Container = styled.div`
display: flex;
justify-content: center;
flex-direction: column;
align-items: center;
line-height: 1.5;
background: url('https://source.unsplash.com/1600x900/?abstract, art') no-repeat center center/cover;
color: white;
div {
    text-align: center;
    background: rgba(0, 0, 0, 0.5);
    padding: 1rem 3rem;
   
}
h1 {
    font-size: 70px;    
}
button {
    transform: scale(1.4);
    margin: 20px 0;
}
a {
    color: white;
    text-decoration: none;
}
`;