import React from 'react'
import styled from 'styled-components'
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';

function Header() {
    return (
        <Container>
            <Main>
                <AccessTimeIcon/>
                <SearchContainer>
                    <Search>
                        <input type="text" placeholder="Search..."/>
                    </Search>
                </SearchContainer>
                <HelpOutlineIcon/>
            </Main>
            <UserContainer>
                <Name>
                    Shashwat
                </Name>
                <UserImage>
                    <img src="https://i.imgur.com/6VBx3io.png" alt="1"/>
                </UserImage>
            </UserContainer>
        </Container>
    )
}

export default Header

const Container = styled.div`
    position: relative;
    background: #350d36;
    color: white;
    display: flex;
    justify-content: center;
    box-shadow: 0 1px 0 0 rgb(255, 255, 255/10%);
    align-items: center;
`
const Main = styled.div`
    display: flex;
    margin: 16px;
    
`

const SearchContainer = styled.div`
    min-width: 400px;
    margin: 0 16px;
`

const Search = styled.div`
    box-shadow: inset 0 0 0 1px rgb(104, 74, 100);
    width: 100%;
    border-radius: 6px;
    display: flex;
    align-items: center;
    input {
        background: transparent;
        color: white;
        border: none;
        padding: 4px 8px;
    }
    input:focus {
        outline: none;
    }
`

const UserContainer = styled.div`
display: flex;
padding-right: 16px;
align-items: center;
position: absolute;
right: 0;
`
const Name = styled.div`
    padding-right: 16px;
`;
const UserImage = styled.div`
    width: 28px;
    height: 28px;
    border: 2px solid white;
    border-radius: 3px;
    img {
        width: 100%;
        z-index: -1;
    }
`;
